}

.submit-card{
    background: #FFFFFF;
    box-shadow: 0px 10px 86px -20px #C8D5F1;
    border-radius: 15px;
    width: 450px;
    display: block;
    margin: 30px auto;
}

.header-submit{
    background: #20225E;
    color: #FFFFFF;
    border-radius: 15px 15px 0 0;
}

.close-submit{
    float : right;
    margin: 12px;
    border-radius: 50%;
    background: #2F316E;
    padding: 5px;
    cursor: pointer;
}


.title-submit{
    font-family: Mulish;
    font-style: normal;
    font-weight: bold;
    line-height: 40px;
    font-size: 22px;
    text-align: center;
    padding: 50px;
    margin-bottom: 0;
}


.form-submit{
    margin: auto;
    display: table;
    padding: 40px;
}

.form-submit label{
    font-family: Mulish;
    font-weight: 700;
    font-size: 14px;
    color: #151749;
}

.form-submit input, .form-submit select{
    display: block;
    background: transparent;
    border-color: transparent;
    border-bottom: 2px solid #ccc;
    color: #151749;
    border-radius: 0;
    transition: all .8s;
    width: 250px;
    padding: 7px 0;
    margin-bottom: 20px;
}

.form-submit input:focus , .form-submit select:focus{
    background: transparent;
    border-color: transparent;
    box-shadow: none;
    border-bottom: 2px solid #20225E;
    outline: none;

}


.form-submit button{
    border: none;
    background: #20225E;
    box-shadow: 0px 20px 20px -16px rgba(2, 6, 127, 0.347738);
    border-radius: 38.5px;
    font-family: Mulish;
    font-weight: bold;
    font-size: 13px;
    color: #FFFFFF;
    padding: 13px 50px;
    margin: 40px auto 0px auto;
    display: block;
    cursor: pointer;
}
